<template>
  <!-- data-opened， 0(opened) 表示没被展开过，1(noOpened) 代表已经被展开过了（或者没有子元素所以没有展开不展开的概念，直接认为是1） -->
  <div
    :class="treeNodeClassName"
    ref="treeNodeRef"
    :data-opened="dataOpened.noOpened"
  >
    <div :class="treeParentClassName">
      <div class="stretch_node_r" v-if="!isRoot">
        <p class="line_r"></p>
      </div>
      <div class="node_content">
        <slot v-bind:data="{ treeNodeData, $treeNodeRefs: $refs }"></slot>
      </div>
    </div>
    <!-- 控制展开/收缩的节点 -->
    <div :class="stretchNodeClassName" v-if="childrenLen">
      <p class="line_l"></p>
      <p
        :class="['line_dot', treeNodeData.isOpen ? 'cut_dot' : 'add_dot']"
        @click="changeOpen"
      ></p>
    </div>
    <div
      :class="treeChildrenClassName"
      v-if="childrenLen"
      v-show="treeNodeData.isOpen"
    >
      <p :class="connectLineClassName" v-if="childrenLen > 1"></p>
      <vue-tree
        v-for="item in treeNodeData.children"
        :key="item.id"
        :treeNodeData="item"
        :isRoot="false"
      >
        <template v-slot:default="slotProps">
          <slot v-bind:data="slotProps.data"></slot>
        </template>
      </vue-tree>
    </div>
  </div>
</template>
<script>
import {
  dataOpened,
  treeParentClassName,
  treeChildrenClassName,
  treeNodeClassName,
  stretchNodeClassName,
  connectLineClassName,
} from "./const";
import { resetTree, updatePartTree } from "./util";

export default {
  name: "vueTree",
  props: {
    // 当前节点是否是根节点
    isRoot: {
      type: Boolean,
      required: false,
      default: true,
    },
    // 当前节点的数据信息
    treeNodeData: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      // treeNode 节点是否已经被展开的 data- 标识
      dataOpened,
      // className 类名
      treeParentClassName,
      treeChildrenClassName,
      treeNodeClassName,
      stretchNodeClassName,
      connectLineClassName,
    };
  },
  computed: {
    childrenLen() {
      return (this.treeNodeData.children || []).length;
    },
  },
  mounted() {
    if (this.isRoot) {
      console.log(this.$refs.treeNodeRef);
      resetTree(this.$refs.treeNodeRef);
    }
  },
  methods: {
    changeOpen() {
      this.treeNodeData.isOpen = !this.treeNodeData.isOpen;
      updatePartTree(this.$refs.treeNodeRef);
    },
  },
};
</script>
<style lang="less" scoped>
@transionDuration: 0.15s;
.tree_node {
  position: relative;
  margin-bottom: 8px;
  font-size: 0;
  white-space: nowrap;
  &:last-child {
    margin-bottom: 0;
  }
  .tree_parent {
    position: relative;
    vertical-align: top;
    display: inline-block;
  }
  @stretchLineW: 8px;
  @stretchNodeH: 15px;
  .stretch_node_r {
    display: inline-block;
    width: @stretchLineW;
    .line_r {
      position: absolute;
      left: -@stretchNodeH / 2;
      top: 50%;
      width: @stretchLineW + @stretchNodeH / 2;
    }
  }
  .node_content {
    display: inline-block;
  }

  .stretch_node {
    position: relative;
    display: inline-block;
    z-index: 10;
    p {
      display: inline-block;
      vertical-align: middle;
    }
    .line_dot {
      width: @stretchNodeH;
      height: @stretchNodeH;
      cursor: pointer;
      user-select: none;
      border-radius: 50%;
      background-size: contain;
      background-position: 50%;
      background-repeat: no-repeat;
      background-color: #8a8a8a;
      transition: all @transionDuration;
      &.add_dot {
        background-image: ~"url('./assets/add-round.png')";
      }
      &.cut_dot {
        background-image: ~"url('./assets/cut-round.png')";
      }
    }
  }
  .line_l,.line_r {
    display: inline-block;
    vertical-align: middle;
    width: @stretchLineW;
    height: 2px;
    background-color: #8a8a8a;
  }
  .tree_children {
    position: relative;
    display: inline-block;
    vertical-align: top;
    .connect_line {
      position: absolute;
      left: -@stretchNodeH / 2;
      top: 0;
      height: 100%;
      width: 2px;
      background-color: #8a8a8a;
      z-index: 6;
    }
  }
}
</style>
